@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --left: #fcde67;
  --right: #5bccf6;
}

* {
  font-family: 'Cabin Sketch', cursive;
}

.block__main {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.block__left {
  display: flex;
  height: 100vh;
  width: 100%;
  background: #fcde67;
  background: var(--left);
  color: #030e12;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.block__right {
  display: flex;
  height: 100vh;
  width: 100%;
  background: #5bccf6;
  background: var(--right);
  color: #030e12;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.block__loading {
  margin: none;
}

.block__loading > img{
  height: 70px;
  width: 80px;
  margin-left: -20px;
}

.block__loading > p {
  margin-top: -20px;
}

.elem__button--right {
  border: none;
  background: #fcde67;
  background: var(--left);
  height: 50px;
  width: 100px;
  margin: 4px;
  transition: all 0.2s;
}

.elem__button--right:active {
  background-color: #3e8e41;
  color: white;
  -webkit-transform: scale(.8);
          transform: scale(.8);
}

.elem__button--left {
  border: none;
  background: #5bccf6;
  background: var(--right);
  height: 50px;
  width: 100px;
  margin: 4px;
  transition: all 0.2s;
}

.elem__button--left:active {
  background-color: #3e8e41;
  color: white;
  -webkit-transform: scale(.8);
          transform: scale(.8);
}

h1 {
  margin: 0;
}

@media screen and (max-width: 768px) {

  .block__main {
    flex-direction: column;
  }

}
